dmx.Component('f7-area-chart', {

    attributes: {
        'width': {
            type: Number,
            default: 640
        },

        'height': {
            type: Number,
            default: 320
        },
        
        'datasets': {
            type: Array,
            default: []
        },

        'datasets:values': {
            type: String,
            default: 'values'
        },

        'datasets:color': {
            type: String,
            default: 'color'
        },

        'datasets:label': {
            type: String,
            default: 'label'
        },

        'line-chart': {
            type: Boolean,
            default: false
        },

        'axis': {
            type: Boolean,
            default: false
        },

        'axis-labels': {
            type: Array,
            default: []
        },

        'tooltip': {
            type: Boolean,
            default: false
        },

        'legend': {
            type: Boolean,
            default: false
        },

        'toggle-datasets': {
            type: Boolean,
            default: false
        },

        'max-axis-labels': {
            type: Number,
            default: 8
        },
    },

    getProps: function() {
        return Object.entries(this.props).reduce(function(o, entry) {
            o[entry[0].replace(/\-(.)?/g, function(m, c) { return c.toUpperCase() })] = entry[1];
            return o;
        }, {});
    },

    getDatasets: function() {
        var datasets = [];
        
        if (Array.isArray(this.props.datasets)) {
            datasets = this.props.datasets.map(function(data) {
                var values = dmx.parse(this.props['datasets:values'], data);
                var color = dmx.parse(this.props['datasets:color'], data);
                var label = dmx.parse(this.props['datasets:label'], data);
                if (!Array.isArray(values)) values = [values];
                var item = { values: values.map(Number) };
                if (color) item.color = String(color);
                if (label) item.label = String(label);
                return item;
            }, this);
        }

        return datasets;
    },

    render: function (node) {
        var self = this, params = Object.assign({
            el: node
        }, this.getProps(), this.getDatasets());

        node.classList.add('area-chart');

        dmx.f7.ready(function(f7) {
            self.component = f7.areaChart.create(params);
        });
    },

    update: function(props) {
        if (!dmx.equal(props, this.props)) {
            var params = Object.assign(this.getProps(), this.getDatasets());
            this.component.update(params);
        }
    },

    destroy: function() {
        if (this.component) {
            this.component.destroy();
            this.component = undefined;
        }
    }

});