dmx.Component('f7-range', {

    initialData: {
        value: null
    },

    attributes: {
        'value': {
            type: Number,
            default: null // defaults to center
        },

        'min': {
            type: Number,
            default: 0
        },

        'max': {
            type: Number,
            default: 100
        },

        'step': {
            type: Number,
            default: 1
        },

        'label': {
            type: Boolean,
            default: false
        },

        'no-draggable-bar': {
            type: Boolean,
            default: false
        },

        'vertical': {
            type: Boolean,
            default: false
        },

        'vertical-reversed': {
            type: Boolean,
            default: false
        },

        'scale': {
            type: Boolean,
            default: false
        },

        'scale-steps': {
            type: Number,
            default: 5
        },

        'scale-sub-steps': {
            type: Number,
            default: 0
        },
    },

    methods: {
        setValue: function(value) {
            if (!this.component) return;
            this.component.setValue(value);
        },
    },

    events: {
        change: Event,
        changed: Event,
    },

    init: function(node) {
        var self = this;

        this.component = Framework7.instance.range.create({
            el: node,
            value: this.props.value,
            min: this.props.min,
            max: this.props.max,
            step: this.props.step,
            label: this.props.label,
            draggableBar: !this.props['no-draggable-bar'],
            vertical: this.props.vertical,
            verticalReversed: this.props['vertical-reversed'],
            scale: this.props.scale,
            scaleSteps: this.props['scale-steps'],
            scaleSubSteps: this.props['scale-sub-steps'],
        });

        this.component.on('change', function(range, value) {
            self.set('value', value);
            self.dispatchEvent('change');
        });

        this.component.on('changed', function() {
            self.dispatchEvent('changed');
        });

        this.set('value', this.component.value);
    },

    render: function(node) {
        var self = this;
        
        dmx.f7.ready(function(f7) {
            self.init(node);
        });
    },

    update: function(props) {
        if (props.value != this.props.value) {
            this.component.setValue(this.props.value);
        }
    },

    destroy: function() {
        if (this.component) {
            this.component.destroy();
            this.component = undefined;
        }
    }

});