dmx.Component('f7-pie-chart', {

    attributes: {
        'datasets': {
            type: Array,
            default: []
        },

        'datasets:value': {
            type: String,
            default: 'value'
        },

        'datasets:color': {
            type: String,
            default: 'color'
        },

        'datasets:label': {
            type: String,
            default: 'label'
        },

        'size': {
            type: Number,
            default: 320
        },

        'tooltip': {
            type: Boolean,
            default: false
        }
    },

    getProps: function() {
        return Object.entries(this.props).reduce(function(o, entry) {
            o[entry[0].replace(/\-(.)?/g, function(m, c) { return c.toUpperCase() })] = entry[1];
            return o;
        }, {});
    },

    getDatasets: function() {
        var datasets = [];
        
        if (Array.isArray(this.props.datasets)) {
            datasets = this.props.datasets.map(function(data) {
                var value = dmx.parse(this.props['datasets:value'], data);
                var color = dmx.parse(this.props['datasets:color'], data);
                var label = dmx.parse(this.props['datasets:label'], data);
                var item = { value: Number(value) };
                if (color) item.color = String(color);
                if (label) item.label = String(label);
                return item;
            }, this);
        }

        return datasets;
    },

    render: function (node) {
        var self = this;

        node.classList.add('pie-chart');

        dmx.f7.ready(function(f7) {
            self.component = f7.pieChart.create(Object.assign({
                el: node
            }, self.getProps(), self.getDatasets()));
        });
    },

    update: function(props) {
        if (!dmx.equal(props, this.props)) {
            this.component.update(Object.assign(this.getProps(), this.getDatasets()));
        }
    },

    destroy: function() {
        if (this.component) {
            this.component.destroy();
            this.component = undefined;
        }
    }

});